@font-face {
  font-family: "RobotoCondensed";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("RobotoCondensed Regular"), local("RobotoCondensed-Regular"), url("../fonts/robotocondensedregular.woff2") format("woff2"), url("../fonts/robotocondensedregular.woff") format("woff");
}

@font-face {
  font-family: "RobotoCondensed";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("RobotoCondensed Bold"), local("RobotoCondensed-Bold"), url("../fonts/robotocondensedbold.woff2") format("woff2"), url("../fonts/robotocondensedbold.woff") format("woff");
}

@font-face {
  font-family: "PTMono";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("PTMono Regular"), local("PTMono-Regular"), url("../fonts/ptmonoregular.woff2") format("woff2"), url("../fonts/ptmonoregular.woff") format("woff");
}



